export default {
  name: "Verification",
  data() {
    return {
      tipwords: "发送验证码",
      telphone: "",
      yzm: "",
      isClick: true,
      readonly: false,
      seconds: 59,
      isVerification: true
    };
  },
  methods: {
    submit() {
      this.isVerification = false;
    },
    sendCode() {
      if (this.isClick == false) {
        console.log("已发送");
        this.isClick = true;
        this.readonly = true;
        let timer = setInterval(() => {
          if (this.seconds == 0) {
            clearInterval(timer);
            this.seconds = 59;
            this.isClick = false;
            this.readonly = false;
          } else {
            this.seconds--;
          }
        }, 1000);
      } else {
        if (this.readonly) {
        } else {
          alert("请输入正确的11位数的手机号码！");
        }
      }
    }
  },
  watch: {
    telphone: function(n, o) {
      if (/^1[34578]\d{9}$/.test(n)) {
        this.isClick = false;
      } else {
        this.isClick = true;
      }
    }
  },
  components: {}
};



