<template>
  <div class="verification padd">
      <div class="yan" v-if="isVerification">
        <h2 class="two_t_m">为确保账户安全，首次购买需手机验证</h2>
        <ul>
          <li>
            <input type="tel" placeholder="手机号码" v-model="telphone" :readonly="readonly">
            <span class="iconfont icon-guanbi ico" v-show="telphone.length&&!readonly" @click="telphone=''"></span>
          </li>
          <li>
            <input type="tel" class="yzm" placeholder="验证码" v-model="yzm">
            <span class="two_t_m ma" :class="{clickstate:isClick}" v-show="!readonly" @click="sendCode">| 发送验证码</span>
            <span class="two_t_m ma clickstate" v-show="readonly">| {{seconds}}s 后重试</span>
          </li>
        </ul>
        <button class="bluebtn submit" @click="submit()">提交</button>
        <p>
          <span class="two_t_m">遇到问题？</span>
        </p>
      </div>
      <div class="success" v-if="!isVerification">
        <p class="success iconfont icon-chenggong"></p>
        <h2 class="one_t tc">手机验证成功</h2>
        <button class="bluebtn geren" @click="$router.push('/shoppingBag/comfirm')">继续下单</button>
        <button class="whitebtn" @click="$router.push('/home/today')">返回商城首页</button>
      </div>
  </div>
</template>

<script>
export default {
  name: "Verification",
  data() {
    return {
      tipwords: "发送验证码",
      telphone: "",
      yzm: "",
      isClick: true,
      readonly: false,
      seconds: 59,
      isVerification: true
    };
  },
  methods: {
    submit() {
      this.isVerification = false;
    },
    sendCode() {
      if (this.isClick == false) {
        console.log("已发送");
        this.isClick = true;
        this.readonly = true;
        let timer = setInterval(() => {
          if (this.seconds == 0) {
            clearInterval(timer);
            this.seconds = 59;
            this.isClick = false;
            this.readonly = false;
          } else {
            this.seconds--;
          }
        }, 1000);
      } else {
        if (this.readonly) {
        } else {
          alert("请输入正确的11位数的手机号码！");
        }
      }
    }
  },
  watch: {
    telphone: function(n, o) {
      if (/^1[34578]\d{9}$/.test(n)) {
        this.isClick = false;
      } else {
        this.isClick = true;
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.verification {
  font-size: 0.16rem;
  h2 {
    text-align: center;
    margin-bottom: 0.15rem;
  }
  ul {
    li {
      position: relative;
      border-bottom: 0.01rem solid #f2f2f2;
      input {
        width: 100%;
        height: 0.52rem;
        border: none;
        padding: 0.15rem 0 0.16rem 0;
      }
      .ma {
        position: absolute;
        top: 0.15rem;
        right: 0;
      }
      .clickstate {
        color: #cccccc;
      }
      .yzm {
        width: 2rem;
      }
      .ico {
        position: absolute;
        top: 0.18rem;
        right: 0.1rem;
      }
    }
  }
  .submit {
    margin: 0.3rem 0rem;
  }
  p {
    text-align: center;
  }
  .success {
    p {
      width: 0.93rem;
      height: 0.93rem;
      font-size: 0.93rem;
      margin: 0 auto;
      margin-top: 0.3rem;
      margin-bottom: 0.33rem;
    }
    .geren {
      margin: 0.3rem 0rem 0.15rem 0rem;
    }
  }
}
</style>



